@import "https://fonts.googleapis.com/css2?family=Outfit&display=swap";
@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@300;700&display=swap";
@import "https://fonts.googleapis.com/css2?family=Poiret+One&display=swap";
@import "https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap";
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

body {
  color: #fff;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #020b12;
  margin: 0;
  padding: 0;
  font-family: Zen Dots, cursive;
}

li {
  list-style: none;
}

:root {
  --bg-color: #020b12;
  --second-bg-color: #000;
  --text-color: #dfdfdf;
  --second-text-color: #9093b1;
  --social-icon-color: #fff;
}

.container-video {
  height: 100vh;
  width: 100%;
  position: relative;
}

.video-text {
  height: 100%;
  width: 100%;
  -webkit-text-stroke: .5px var(--text-color);
  color: rgba(0, 0, 0, 0);
  background-color: #000;
  justify-content: center;
  align-items: center;
  font-size: 110px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.big-logo span {
  transition: all .5s;
  animation: animateBigLogo 2.5s infinite;
  display: inline-block;
}

.big-logo {
  transition: all .8s;
}

.big-logo:hover {
  transform: scale(1.08);
}

@keyframes animateBigLogo {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #000;
  }
}

.click-container {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: flex;
  position: absolute;
  top: 85%;
  left: 49%;
}

.banner-text {
  cursor: pointer;
  color: var(--text-color);
  border: 0;
  margin-bottom: 30px;
  font-family: Outfit;
  font-size: 30px;
}

.banner-text:after {
  content: "";
  height: 2px;
  width: 0%;
  background-color: var(--text-color);
  transition: all .5s ease-in-out;
  display: block;
}

.banner-text:hover:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #5577fe;
  display: block;
}

.arrow-down span {
  width: 30px;
  height: 30px;
  border-bottom: 5px solid #1e34ff;
  border-right: 5px solid #ff2c2c;
  margin: -16px;
  animation: arrow-down 2s infinite;
  display: block;
  transform: rotate(45deg);
}

.arrow-down span:nth-child(2) {
  animation-delay: -.2s;
}

.arrow-down span:nth-child(3) {
  animation-delay: -.4s;
}

@keyframes arrow-down {
  0% {
    opacity: 0;
    transform: rotate(45deg)translate(-20px, -20px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(45deg)translate(20px, 20px);
  }
}

.page-header {
  justify-content: space-between;
  align-items: center;
  padding: 2rem 14%;
  display: flex;
  top: 0;
  left: 0;
}

.logo {
  color: #f3f0ed;
  font-family: Montserrat;
  font-size: 28px;
  font-weight: 700;
}

.logo span {
  color: #ebebeb;
  transition: all .5s;
  animation: animate 2s infinite;
  display: inline-block;
}

@keyframes animate {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #2f2bf8;
  }
}

.logo-small {
  color: #f3f0ed;
  font-family: Montserrat;
  font-size: 28px;
  font-weight: 300;
}

.navbar {
  display: flex;
}

.navbar a {
  color: var(--text-color);
  margin-left: 4rem;
  font-size: 1.7rem;
  transition: all .5s;
}

.wrapper {
  display: none;
}

.navbar a:after {
  content: "";
  height: 2px;
  width: 0%;
  background-color: var(--text-color);
  transition: all .3s;
  display: block;
}

.navbar a:hover:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #5577fe;
  display: block;
}

.home {
  min-height: 100vh;
  justify-content: space-between;
  padding: 260px 16% 1rem;
  display: flex;
}

.name {
  -webkit-text-stroke: 1px var(--text-color);
  color: rgba(0, 0, 0, 0);
  font-family: Montserrat;
  font-size: 5.6rem;
  font-weight: 700;
  line-height: 1.3;
}

.home_content {
  width: 1000px;
}

.home h3 {
  font-family: Montserrat;
  font-size: 3.2rem;
  font-weight: 700;
}

.home h3:nth-of-type(2) {
  margin-bottom: 2rem;
}

.home_content p {
  font-size: 1.6rem;
}

.text-1 {
  margin-top: 16px;
}

.developer-text span {
  color: #ebebeb;
  transition: all .5s;
  animation: front-animate 2s infinite;
  display: inline-block;
}

@keyframes front-animate {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #2f2bf8;
  }
}

.home-img {
  min-width: 700px;
  height: 560px;
  background: linear-gradient(#04111b, #000);
  margin-left: 52px;
  position: relative;
}

.home-img:before, .home-img:after {
  content: "";
  z-index: -1;
  background: linear-gradient(45deg, #ff2d2d, #2c2cff, #04121d, #ff4d4d, #effcf9, #973d3d, #55fd63, #04121d, #4075e9) 0 0 / 500%;
  animation: amimate 30s infinite;
  position: absolute;
  top: -2px;
  bottom: -2px;
  left: -2px;
  right: -2px;
}

.home-img:after {
  filter: blur(25px);
}

.home-img img {
  width: 700px;
  height: 560px;
  position: absolute;
  bottom: 0;
}

@keyframes amimate {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 300% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.social-media a {
  width: 4rem;
  height: 4rem;
  border: .2rem solid var(--second-text-color);
  color: var(--social-icon-color);
  background: none;
  border-radius: 50%;
  justify-content: center;
  margin: 3rem 1.5rem 3rem 0;
  padding: 12px;
  font-size: 2rem;
  transition: all .5s;
  display: inline-flex;
}

.social-media a:nth-child(1):hover {
  color: #446dc6;
  box-shadow: 0 0 50px #446dc6;
}

.social-media a:nth-child(2):hover {
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285aeb 90%);
  transition: all .5s;
  box-shadow: -6px -4px 20px #10f, 7px -6px 20px #6802bb, 7px 2px 20px #fa1776, -4px 6px 20px rgba(255, 219, 14, .937);
}

.social-media a:nth-child(3):hover {
  color: #00bfff;
  box-shadow: 0 0 34px #00bfff;
}

.social-media a:nth-child(4):hover {
  color: #fff;
  z-index: -1;
  background: linear-gradient(30deg, #5c5c5c, #030303, #fffefe, #8b8b8b) 0 0 / 300%;
  animation: amimate 20s;
  box-shadow: 0 0 30px #fff;
}

.btn-download {
  background: var(--second-bg-color);
  letter-spacing: .1rem;
  border-radius: 4rem;
  padding: 1rem 2.8rem;
  font-size: 1.6rem;
  font-weight: 600;
  transition: all .3s;
  display: inline-block;
  box-shadow: 0 0 20px #6461ff;
}

.btn-download:hover {
  box-shadow: none;
  background: #1b1b1b;
}

.about {
  padding: 40px 16% 1rem;
}

.circle-logo {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px 9% 2rem;
  font-family: Poiret One;
  font-size: 24px;
  display: flex;
}

.text-logo-about span {
  color: #0a0a0a;
  transition: all .5s;
  animation: logo-about 2s infinite;
  display: inline-block;
}

@keyframes logo-about {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #ff2c2c;
  }
}

.about {
  min-height: 100vh;
  flex-direction: row-reverse;
  justify-content: space-between;
  padding: 240px 16% 1rem;
  display: flex;
}

.about-img {
  min-width: 500px;
  height: 550px;
  background: linear-gradient(#04111b, #000);
  margin-right: 60px;
  position: relative;
}

.about-img:before, .about-img:after {
  content: "";
  z-index: -1;
  background: linear-gradient(45deg, #ff2d2d, #2c2cff, #04121d, #ff4d4d, #effcf9, #973d3d, #2c2cff, #04121d, #4075e9) 0 0 / 500%;
  animation: amimate-about 30s infinite;
  position: absolute;
  top: -2px;
  bottom: -2px;
  left: -2px;
  right: -2px;
}

@keyframes amimate-about {
  0% {
    background-position: 40px 0;
  }

  50% {
    background-position: 300% 0;
  }

  100% {
    background-position: 100px 0;
  }
}

.about-img:after {
  filter: blur(25px);
}

.about-img img {
  width: 500px;
  height: 550px;
  position: absolute;
  bottom: 0;
}

.text-content {
  width: 1000px;
}

.heading {
  color: #9f9d9d;
  text-align: center;
  font-size: 4.5rem;
}

.text-content h2 {
  text-align: left;
  margin-top: -1rem;
  line-height: 1.2;
}

.text-content p {
  margin: 2rem 0 3rem;
  font-size: 1.6rem;
}

.heading span {
  -webkit-text-stroke: 1px var(--text-color);
  color: rgba(0, 0, 0, 0);
  font-family: Montserrat;
}

.btn-about {
  background: var(--second-bg-color);
  letter-spacing: .1rem;
  border-radius: 4rem;
  padding: .5rem 1.4rem;
  font-size: 1.4rem;
  font-weight: 600;
  transition: all .3s;
  display: inline-block;
  box-shadow: 0 0 20px #4b75ff;
}

.btn-about:hover {
  box-shadow: none;
  background: #1b1b1b;
}

.text-logo-servises span {
  color: #0a0a0a;
  transition: all .5s;
  animation: logo-servises 2s infinite;
  display: inline-block;
}

@keyframes logo-servises {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #000;
  }
}

.servises {
  padding: 40px 16% 1rem;
}

.servises h2 {
  margin: 1rem 0 7rem;
}

.servises h3 {
  margin-bottom: 1.3rem;
  font-size: 1.5rem;
}

.servises p {
  margin-bottom: 1.3rem;
  font-size: 1.2rem;
  transition: all .1s;
}

.servises p:hover {
  cursor: pointer;
  transform: scale(1.02);
}

.heading-servises, .healing-portfolio, .heading-contact {
  color: #9f9d9d;
  text-align: center;
  font-size: 4.5rem;
}

.heading-servises span, .healing-portfolio span, .heading-contact span {
  -webkit-text-stroke: 1px var(--text-color);
  color: rgba(0, 0, 0, 0);
  font-family: Montserrat;
}

.servises-container {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-bottom: 27rem;
  display: flex;
  position: relative;
}

.servises-box {
  text-align: center;
  background-color: #5f5f5f;
  border: 5px double #fff;
  border-radius: 20px;
  flex: 30rem;
  padding: 3rem 2rem 4rem;
  transition: all .1s;
}

.servises-box:before {
  content: "";
  z-index: -1;
  background: linear-gradient(90deg, #020b12, rgba(239, 252, 249, .663), #020b12) 0 0 / 170%;
  border-radius: 40px;
  animation: amimateServises 30s infinite;
  position: absolute;
  top: -20px;
  bottom: -20px;
  left: -20px;
  right: -20px;
}

@keyframes amimateServises {
  0% {
    background-position: 0 0;
  }

  20% {
    background-position: 700% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.servises-box i {
  -webkit-text-stroke: 1px var(--text-color);
  color: #fff;
  margin-bottom: 2rem;
  font-size: 7rem;
}

.servises-box .js {
  color: #ffd722;
  background-color: #000;
}

.servises-box .paint {
  color: #fff;
  cursor: pointer;
}

.servises-box .paint:hover {
  color: #00f;
  animation: color-change 5s linear infinite;
}

@keyframes color-change {
  0% {
    color: #f2f2ff;
  }

  5% {
    color: #828282;
  }

  10% {
    color: #5c7fd6;
  }

  15% {
    color: #245fff;
  }

  20% {
    color: #793cfc;
  }

  25% {
    color: #9a2aa2;
  }

  30% {
    color: #d90d43;
  }

  35% {
    color: #ff3b3b;
  }

  40% {
    color: #ff8725;
  }

  45% {
    color: #fed039;
  }

  50% {
    color: #f0f42f;
  }

  55% {
    color: #afff24;
  }

  60% {
    color: #64d039;
  }

  65% {
    color: #4be56a;
  }

  70% {
    color: #42c3a7;
  }

  75% {
    color: #71e69a;
  }

  80% {
    color: #8fd2a6;
  }

  85% {
    color: #72ae8c;
  }

  90% {
    color: #8aa189;
  }

  95% {
    color: #9b9898;
  }

  100% {
    color: #c7c7c9;
  }
}

.servises-box .devices {
  color: #5076ff;
}

.servises-box a {
  background: var(--second-bg-color);
  letter-spacing: .1rem;
  border-radius: 4rem;
  padding: 1rem 1.8rem;
  font-size: 1rem;
  transition: all .3s;
  display: inline-block;
}

.servises-box a:hover {
  color: var(--second-bg-color);
  box-shadow: none;
  background-color: #ebebeb;
}

.text-logo-portfolio span {
  color: #0a0a0a;
  transition: all .5s;
  animation: logo-portfolio 2s infinite;
  display: inline-block;
}

@keyframes logo-portfolio {
  0% {
    color: #fff;
    margin: 0;
    transform: translateY(0);
  }

  10% {
    color: #1f1f1f;
  }
}

.portfolio {
  background-color: #1d1d1d;
  padding: 40px 12% 1rem;
}

.portfolio h2 {
  margin-bottom: 4rem;
}

.portfolio-container {
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  gap: 2.5rem;
  margin-bottom: 33rem;
  display: grid;
}

.portfolio-container .portfolio-box {
  border-radius: 1.7rem;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 .1rem 1.4rem #000;
}

.portfolio-box img {
  width: 100%;
  transition: all .5s;
}

.portfolio-box img:hover {
  transform: translateY(0);
}

.portfolio-box .portfolio-layer {
  width: 100%;
  height: 100%;
  text-align: center;
  background: linear-gradient(rgba(0, 0, 0, .8), #242424);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 4rem;
  transition: all .5s;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.portfolio-box:hover .portfolio-layer {
  transform: translateY(0);
}

.portfolio-layer h4 {
  font-size: 3rem;
}

.portfolio-layer p {
  margin: .3rem 0 1rem;
  font-size: 1.4rem;
}

.portfolio-layer a {
  width: 5rem;
  height: 5rem;
  background-color: #555;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.portfolio-layer a:hover {
  background-color: #a0a0a0;
}

.portfolio-layer a i {
  color: #000;
  font-size: 2rem;
}

.contact {
  background-color: #1e1e1e;
}

.heading-contact {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.contact-list {
  justify-content: center;
  align-items: center;
  gap: 4rem;
  margin-bottom: 2rem;
  display: flex;
}

.contact-list li {
  align-items: center;
  display: flex;
}

.line {
  width: 100%;
  height: 0;
  border: 1px solid rgba(255, 255, 255, .15);
  justify-content: center;
  align-items: center;
  margin-bottom: 1.5rem;
  display: flex;
}

.contact .copyright {
  text-align: center;
  color: rgba(196, 196, 196, .5);
  margin-bottom: 1.5rem;
  font-family: Ubuntu;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 34px;
}

.contact-item a {
  align-items: center;
  gap: 1rem;
  font-size: 20px;
  display: flex;
}

.contact-item a:hover {
  color: #6181ff;
}

.contact-item i {
  width: 4rem;
  height: 4rem;
  background-color: #4a4a4a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  transition: all .3s;
  display: flex;
  box-shadow: 0 0 1rem #000;
}

.contact-item i:hover {
  color: #4268ff;
  background-color: #9f9d9d;
  transform: scale(1.1);
}

.arrow-down {
  margin-left: 2rem;
}

.arrow-down i {
  border-radius: 20%;
}

.logo-details img {
  width: 48px;
  cursor: pointer;
  margin-right: 12px;
}

.logo-details i {
  margin-bottom: -2px;
}

.sidebar {
  height: 734px;
  width: 78px;
  z-index: 99;
  background: rgba(126, 126, 126, .35);
  border-radius: 0 18px 18px 0;
  padding: 6px 14px;
  transition: all .5s;
  position: fixed;
  top: 20%;
  left: 0;
}

.sidebar.open {
  width: 250px;
  height: 734px;
  top: 20%;
  left: 0;
}

.sidebar .logo-details {
  height: 60px;
  align-items: center;
  display: flex;
  position: relative;
}

.sidebar .logo-details .icon {
  opacity: 0;
  transition: all .5s;
}

.sidebar .logo-details .logo_name {
  color: #fff;
  opacity: 0;
  font-size: 20px;
  font-weight: 600;
  transition: all .5s;
}

.sidebar.open .logo-details .icon, .sidebar.open .logo-details .logo_name {
  opacity: 1;
}

.logo_name {
  letter-spacing: .2em;
  font-family: Poiret One;
  font-size: 20px;
}

.logo-details {
  align-items: center;
  display: flex;
}

.sidebar .logo-details #btn {
  text-align: center;
  cursor: pointer;
  font-size: 23px;
  transition: all .5s;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.sidebar.open .logo-details #btn {
  text-align: right;
  margin-top: 4px;
}

.sidebar i {
  color: #fff;
  height: 60px;
  min-width: 50px;
  text-align: center;
  font-size: 28px;
  line-height: 60px;
}

.sidebar .nav-list {
  height: 100%;
  margin-top: 20px;
}

.sidebar li {
  margin: 8px 0;
  list-style: none;
  position: relative;
}

.sidebar li .tooltip {
  z-index: 3;
  color: #020b12;
  opacity: 0;
  white-space: nowrap;
  pointer-events: none;
  background: #fff;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 15px;
  font-weight: 400;
  transition: all;
  position: absolute;
  top: -20px;
  left: calc(100% + 15px);
  box-shadow: 0 5px 10px rgba(0, 0, 0, .3);
}

.sidebar li:hover .tooltip {
  opacity: 1;
  pointer-events: auto;
  transition: all .4s;
  top: 50%;
  transform: translateY(-50%);
}

.sidebar.open li .tooltip {
  display: none;
}

.sidebar li a {
  height: 100%;
  width: 100%;
  background: #11101d;
  border-radius: 12px;
  align-items: center;
  text-decoration: none;
  transition: all .4s;
  display: flex;
}

.sidebar li a:hover {
  background: #fff;
}

.sidebar li a .links_name {
  color: #fff;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  font-size: 15px;
  font-weight: 400;
  transition: all .4s;
}

.sidebar.open li a .links_name {
  opacity: 1;
  pointer-events: auto;
}

.sidebar li a:hover .links_name, .sidebar li a:hover i {
  color: #11101d;
  transition: all .5s;
}

.sidebar li i {
  height: 50px;
  border-radius: 12px;
  font-size: 18px;
  line-height: 50px;
}

.sidebar li.profile {
  height: 60px;
  width: 78px;
  background: #1d1b31;
  padding: 10px 14px;
  transition: all .5s;
  position: fixed;
  bottom: -8px;
  left: 0;
